/* Slider */

.slick-list, .slick-slider {
	position: relative;
	display: block
}

.slick-slider {
	box-sizing: border-box;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-touch-callout: none;
	-khtml-user-select: none;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent
}

.slick-list {
	overflow: hidden;
	margin: 0;
	padding: 0
}

.slick-list:focus {
	outline: 0
}

.slick-list.dragging {
	cursor: pointer;
	cursor: hand
}

.slick-slider .slick-list, .slick-slider .slick-track {
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0)
}

.slick-track {
	position: relative;
	top: 0;
	left: 0;
	display: block
}

.slick-track:after, .slick-track:before {
	display: table;
	content: ''
}

.slick-track:after {
	clear: both
}

.slick-loading .slick-track {
	visibility: hidden
}

.slick-slide {
	display: none;
	float: left;
	position: relative;
	height: 100%;
	min-height: 1px
}

[dir=rtl] .slick-slide {
	float: right
}

.slick-initialized .slick-slide, .slick-slide img {
	display: block
}

.slick-slide.slick-loading img {
	display: none
}

.slick-slide.dragging img {
	pointer-events: none
}

.slick-loading .slick-slide {
	visibility: hidden
}

.slick-vertical .slick-slide {
	display: block;
	height: auto;
	border: 1px solid transparent
}

.slick-arrow.slick-hidden {
	display: none
}

.slick-slide-item {
	float: left;
	width: auto;
	height: 100%;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden
}

/* video bg */

.background-video {
	bottom: 0;
	left: 0;
	overflow: hidden;
	position: absolute;
	right: 0;
	top: 0
}

.ytplayer-container, source, video {
	bottom: 0;
	left: 0;
	min-width: 100%;
	position: absolute
}

source, video {
	min-height: 100%
}

.loaded .ytplayer-container {
	display: block
}

.ytplayer-container {
	height: 100%;
	overflow: hidden;
	right: 0;
	top: 0;
	z-index: 0
}

.ytplayer-shield {
	height: 100%;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 2
}

.ytplayer-player, .ytplayer-shield {
	position: absolute
}

