/*-------------Fonts---------------------------------------*/

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800,900|Nunito:400,600,700,800,900&amp;subset=cyrillic,cyrillic-ext,latin-ext,vietnamese');
/*-------------General Style---------------------------------------*/

html {
	overflow-x: hidden !important;
	height: 100%;
}

body {
	font-weight: 600;
	font-size: 13px;
	height: 100%;
	width: 100%;
	margin: 0px;
	padding: 0px;
	text-align: center;
	background: transparent;
	color: #000;
	font-family: 'Nunito', sans-serif;

}

@-o-viewport {
	width: device-width;
}

@-ms-viewport {
	width: device-width;
}

@viewport {
	width: device-width;
}

/*--------------Typography--------------------------------------*/

p {
	font-size: 13px;
	line-height: 24px;
	padding-bottom: 10px;
	font-weight: 500;
	color: #878C9F;
}

blockquote {
	float: left;
	padding: 10px 20px;
	margin: 0 0 20px;
	font-size: 17.5px;
	border-left: 15px solid #eee;
	position: relative;
}

blockquote:before {
	font-family: Font Awesome\ 5 Pro;
	content: "\f10e";
	position: absolute;
	color: #ccc;
	bottom: 3px;
	font-size: 43px;
	right: 6px;
	opacity: 0.4
}

blockquote p {
	font-family: Georgia, "Times New Roman", Times, serif;
	font-style: italic;
	color: #494949;
}

/*------ loader-------------------------------------------------*/

.loader-wrap {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 100;
	background: #18458B;
}

.pin, .ajax-loader {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 40px;
	height: 40px;
	border: 4px solid;
	border-radius: 100%;
	margin: -20px 0 0 -20px;
}

.pulse, .ajax-loader-cirle {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-radius: 100%;
	animation: spin 1000ms linear infinite;
}

.pulse:before, .ajax-loader-cirle:before {
	content: '';
	position: absolute;
	width: 12px;
	height: 4px;
	border-radius: 4px;
	background: #fff;
	left: 50%;
	top: 50%;
	margin: -2px 0 0 -12px;
}

@keyframes spin {
	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

