/*-------------accordion---------------------------------------*/

.accordion {
	float: left;
	width: 100%;
	position: relative;
}

.accordion a.toggle {
	border: 1px solid #eee;
	padding: 18px 50px 18px 30px;
	float: left;
	width: 100%;
	position: relative;
	background: #fff;
	border-radius: 4px;
	box-sizing: border-box;
	text-align: left;
	font-size: 13px;
	font-weight: 700;
	color: #666;
	-webkit-transition: all 300ms linear;
	transition: all 300ms linear;
	margin-bottom: 15px;
}

.accordion a.toggle span {
	position: absolute;
	right: -1px;
	top: -1px;
	bottom: -1px;
	width: 60px;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
}

.accordion a.toggle span:before, .accordion a.toggle span:after {
	content: '';
	position: absolute;
	background: #fff;
	top: 50%;
	left: 50%;
	-webkit-transition: all 300ms linear;
	transition: all 300ms linear;
}

.accordion a.toggle span:before {
	width: 10px;
	height: 2px;
	margin-top: -1px;
	margin-left: -5px;
}

.accordion a.toggle span:after {
	width: 2px;
	height: 10px;
	margin-top: -5px;
	margin-left: -1px;
}

.accordion a.toggle.act-accordion {
	color: #fff;
	border-color: transparent;
}

.accordion a.toggle.act-accordion span:after {
	height: 0;
}

.accordion-inner {
	display: none;
	float: left;
	width: 100%;
	position: relative;
	padding: 20px 30px 15px;
	background: #fff;
	margin: 0 0 15px;
	text-align: left;
	border-radius: 4px;
	border: 1px solid #eee;
}

.accordion-inner.visible {
	display: block;
}

